<template>
  <div class="w-100">
    <h3 v-if="connection && app_name == 'pbxplus'" class="mb-1 mt-3 font-size-md text-center">{{ phone_number }}</h3>
    <p v-if="connection" class="mb-0 font-size-sm text-center">{{ this.duration }}</p>
  </div>
</template>

<script>
  export default {
    props: {
      connection: Object,
      phone_number: String,
      app_name: String,
    },
    data() {
      return {
        duration: "",
        timeInterval: "",
      };
    },
    mounted: function() {
      //   incrementTimer() {
      var input = {
        day: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
      };
      var timestamp = new Date(input.day, input.hours, input.minutes, input.seconds);
      var interval = 1;
      this.timeInterval = setInterval(() => {
        timestamp = new Date(timestamp.getTime() + interval * 1000);
        this.duration = `${timestamp.getHours() < 10 ? "0" + timestamp.getHours() : timestamp.getHours()} :
                          ${timestamp.getMinutes() < 10 ? "0" + timestamp.getMinutes() : timestamp.getMinutes()} :
                          ${timestamp.getSeconds() < 10 ? "0" + timestamp.getSeconds() : timestamp.getSeconds()}`;
      }, Math.abs(interval) * 1000);
      //   },
    },
  };
</script>

